<script>
/* 表单状态formState: 0预览，1新建，2编辑，3查看 */

// import ReferenceSearchTable from "@/views/system/field-widget/reference-search-table";
import FormState from "@/views/system/form-state-variables";
import {
  isDefinedNull,
  isNull,
  isNotNull,
  isEmptyStr,
  arrayContain,
} from "@/utils/util";
// import * as SYS from "@/api/system-manager";
import { baseURL } from "@/config";
// import { listRole } from "@/api/user";
import {
  initDataList,
  createRecord,
  updateRecord,
  saveRecord,
  deleteRecord,
  getDataList,
  getColumnShowNoPage,
  getDataBySql,
} from "@/api";
import Vue from "vue";
import { createLayoutObj } from "@/views/system/layout/form-layout-object";
export default {
  name: "FieldWidget",
  props: {
    crudMethods: {
      type: Object,
      default: () => {
        return {
          initDataList: initDataList,
          createRecord: createRecord,
          updateRecord: updateRecord,
          saveRecord: saveRecord,
          deleteRecord: deleteRecord,
        };
      },
    },
    entity: { type: Object, default: () => {} },
    labelWidth: {
      type: Number,
      default: 75,
    },
    labelAlign: {
      type: String,
      default: "label-left-align",
    },
    field: { type: Object, default: () => {} },
    fieldProps: { type: Object, default: () => {} },
    fieldPropsMap: { type: Object, default: () => {} },
    formModel: {
      type: Object,
      default: () => {},
    },
    labelsModel: {
      type: Object,
      default: () => {},
    },
    formState: {
      type: Number,
      default: 0,
    },
    layout: { type: Object, default: () => {} },
  },
  data() {
    return {
      showReferenceDialogFlag: false,
      formWidgetLayout: {},
      formWidgetFormModel: {},
      formModelCopy: {},
      formWidgetLabelsModel: {},
      formWidgetFieldPropsMap: {},
      formWidgetFormState: {},
      curRefField: null,

      roleList: [], //权限角色列表

      options: [],

      uploadBtnHidden: false,
      fileList: [], //上传文件列表
      typeObj: {
        1: "Integer",
        2: "Decimal",
        3: "Text",
        4: "Textarea",
        5: "Option",
        6: "Tag",
        7: "Date",
        8: "DateTime",
        9: "Year",
        10: "Month",
        11: "Time",
        12: "File",
        13: "Picture",
      },
      tableName: "",
    };
  },
  computed: {
    SubformTitle() {
      // console.log(this.layout)
      return this.field && this.field.associated_subform
        ? this.field.associated_tablename_zh
        : "子表单";
      // return '子表单'
    },
    isReadOnly() {
      return (
        this.formState === FormState.PREVIEW ||
        this.formState === FormState.VIEW ||
        (this.formState === FormState.NEW && !this.fieldProps.creatable) ||
        (this.formState === FormState.EDIT && !this.fieldProps.updatable) ||
        this.field.is_edit == 0
      );
    },

    fieldValue: {
      get() {
        if(this.formState==0){
          if(this.field.type === 15||this.field.type === 6||this.field.type === 'many'){
            return []
          }else {
            return ''
          }

        }else if (this.field.type === 13 || this.field.type === 12) {
          return this.formModel[this.field.name];
        } else if (this.field.type == "many") {
          if (_.isArray(this.formModel[this.field.name])) {
            let data = [];
            _(this.formModel[this.field.name]).forEach((obj) => {
              let json = {};
              for (let name in obj) {
                if (this.fieldProps.fieldList[name].type == "6") {
                  if (_.isArray(obj[name])) {
                    json[name] = obj[name];
                  } else {
                    json[name] = obj[name] ? obj[name].split(",") : [];
                  }
                } else {
                  json[name] = obj[name];
                }
              }
              data.push(json);
            });
            return data;
          } else {
            return this.formModel[this.field.name];
          }
          return this.formModel[this.field.name];
        } else if (this.field.type === 16 && this.formState == 1) {
          this.$emit("setFormModel", {
            key: this.field.name,
            value: this.userrole.signature,
          });
          return this.userrole.signature;
        } else if (
          this.field.type === 5 &&
          this.field.dictortsql == 2 &&
          this.field.field_sql
        ) {
          // this.remoteMethod({
          //   code: this.formModel[this.field.name],
          //   sql: this.field.field_sql,
          // })
          return this.formModel[this.field.name];
        } else if (this.field.type === 15 || this.field.type === 6) {
          // if (
          //   this.field.type === 6 &&
          //   this.field.dictortsql == 2 &&
          //   this.field.field_sql &&
          //   this.formModel[this.field.name]
          // ) {
          //   this.remoteMethod({
          //     code:
          //       typeof this.formModel[this.field.name] === 'string'
          //         ? this.formModel[this.field.name]
          //         : this.formModel[this.field.name].split(','),
          //     sql: this.field.field_sql,
          //   })
          // }
          let val = this.formModel[this.field.name];
          return val ? (typeof val === "string" ? val.split(",") : val) : [];
        } else if (!this.formModel || isNull(this.formModel[this.field.name])) {
          return "";
        } else {
          return this.formModel[this.field.name];
        }
      },
      set(newValue) {
        if (this.formState !== 0) {
          /* 预览状态下formModel为null，下一行会报错！！ */
          if (this.field.type === 15 || this.field.type === 6) {
            this.$emit("setFormModel", {
              key: this.field.name,
              value: newValue.join(","),
            });
          } else if (this.field.type == "many") {
            if (_.isArray(newValue)) {
              let dataArr = [];
              _(newValue).forEach((obj) => {
                let datajson = {};
                for (let name in obj) {
                  if (this.fieldProps.fieldList[name].type == "6") {
                    datajson[name] = obj[name] ? obj[name].join(",") : "";
                  } else {
                    datajson[name] = obj[name];
                  }
                }
                dataArr.push(datajson);
              });
              this.$emit("setFormModel", {
                key: this.field.name,
                value: dataArr,
                flag: "arr",
              });
            } else {
              this.$emit("setFormModel", {
                key: this.field.name,
                value: newValue,
              });
            }
          } else {
            this.$emit("setFormModel", {
              key: this.field.name,
              value: newValue,
            });
          }
          // this.$emit('setFormModel', {
          //   key: this.field.name,
          //   value: newValue,
          // })
          //赋值
          if (this.field.assignment_formula) {
            // [gj]=='ABW'～ [xb] != '0'#'9',
            // [gj]=='ABW'～ [xb] = '1',
            // [gj]=='ABW'～ ![dx2],
            // [gj]=='ABW'～ *[csrq],
            // [gj]=='AFG'～ ?[dx2]
            // [gj]=='ABW' ～ {0}
            let formulas = this.field.assignment_formula.split(",");
            formulas.forEach((item) => {
              let arr = item.split("～");
              let condition = arr[0];
              let calculation = arr[1];
              if (condition.includes("[")) {
                if (calculation.includes("{")) {
                  const conditionFields = condition.match(/(?<=\[).*?(?=\])/gi);
                  conditionFields.forEach((field, index) => {
                    if (this.formModel[field] == undefined) {
                      console.error("赋值公式错误，请重新修改！");
                      return;
                    }
                    condition = condition.replace(
                      `[${field}]`,
                      `'${this.formModel[field]}'`
                    );
                  });
                  if (eval(condition)) {
                    const ids = JSON.parse(
                      this.fieldPropsMap[this.field.name].associated_table_id
                    );
                    const names = JSON.parse(
                      this.fieldPropsMap[this.field.name].associated_table_name
                    );
                    const namezhs = JSON.parse(
                      this.fieldPropsMap[this.field.name]
                        .associated_tablename_zh
                    );
                    const i = calculation.match(/(?<=\{).*?(?=\})/gi);

                    this.$emit("openSubForm", {
                      tableId: ids[i],
                      tableName: names[i],
                      subformTitle: namezhs[i],
                    });
                    // this.addNewRecord()
                  }
                  return;
                }
                const calculationField =
                  calculation.match(/(?<=\[).*?(?=\])/gi);
                if (calculationField.length != 1) {
                  console.error("赋值公式错误，请重新修改！");
                  return;
                }
                const conditionFields = condition.match(/(?<=\[).*?(?=\])/gi);
                conditionFields.forEach((field, index) => {
                  if (this.formModel[field] == undefined) {
                    console.error("赋值公式错误，请重新修改！");
                    return;
                  }
                  condition = condition.replace(
                    `[${field}]`,
                    `'${this.formModel[field]}'`
                  );
                });
                if (calculation.includes("!=")) {
                  const value = calculation.split("!=")[1];
                  const values = [];
                  value.split("#").forEach((v) => {
                    values.push(v.trim());
                  });
                  if (calculationField[0]) {
                    this.$emit("setAttr", {
                      fields: calculationField,
                      attr: "disabledValues",
                      flag: eval(condition) ? values.join(",") : "",
                    });
                  } else {
                    console.error("联动公式错误，请修改！");
                  }
                } else if (calculation.includes("=")) {
                  const val = calculation.split("=")[1].trim();
                  if (eval(condition)) {
                    this.$emit("setFormModel", {
                      key: calculationField[0],
                      value: val,
                    });
                  }
                } else if (calculation.includes("!")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "is_edit",
                    flag: eval(condition) ? 0 : 1,
                  });
                } else if (calculation.includes("*")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "is_null",
                    flag: eval(condition) ? 1 : 0,
                  });
                } else if (calculation.includes("?")) {
                  this.$emit("setAttr", {
                    fields: calculationField,
                    attr: "linkageImplication",
                    flag: eval(condition) ? 0 : 1,
                  });
                }
              } else {
                try {
                  let arr = calculation.split("=");
                  const fields = calculation.match(/(?<=\[).*?(?=\])/gi);
                  let condition = arr[1];
                  const assignmentField = fields[0];
                  const otherFields = fields.slice(1);

                  otherFields.forEach((field, index) => {
                    if (this.formModel[field] == undefined) {
                      console.error("联动公式错误，请修改！");
                      return;
                    }
                    condition = condition.replace(
                      `[${field}]`,
                      `'${this.formModel[field]}'`
                    );
                  });
                  this.$emit("setFormModel", {
                    key: assignmentField,
                    value: eval(condition),
                  });
                } catch (e) {
                  console.error("联动公式错误，请修改！");
                }
              }
            });
          }

          // if (
          //   this.field.association_value &&
          //   this.field.associated_tablename_zh &&
          //   this.field.associated_table_name
          // ) {
          //   if (newValue == this.field.association_value) {
          //     this.addNewRecord()
          //   }
          // }
        }
      },
    },

