    minValue() {
      if (
        !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.minValue)
      ) {
        return this.fieldProps.fieldViewModel.minValue;
      } else if (this.field.type === 1) {
        return -4200000000;
      } else if (this.field.type === "Percent") {
        return -100000000;
      } else if (this.field.type === 2) {
        return -9999999999999.99;
      } else if (this.field.type === "Money") {
        return -9999999999999.99;
      } else {
        return -4200000000;
      }
    },

    maxValue() {
      if (
        !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.maxValue)
      ) {
        return this.fieldProps.fieldViewModel.maxValue;
      } else if (this.field.type === 1) {
        return 4200000000;
      } else if (this.field.type === "Percent") {
        return 100000000;
      } else if (this.field.type === 2) {
        return 9999999999999.99;
      } else if (this.field.type === "Money") {
        return 9999999999999.99;
      } else {
        return 4200000000;
      }
    },

    precision() {
      if (
        !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.precision)
      ) {
        return this.fieldProps.fieldViewModel.precision;
      } else if (this.field.type === 1) {
        return 0;
      } else if (this.field.type === 2) {
        return 2;
      } else if (this.field.type === "Money") {
        return 2;
      } else {
        return 0;
      }
    },

    minLength() {
      if (
        !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.minLength)
      ) {
        return this.fieldProps.fieldViewModel.minLength;
      } else {
        return 0;
      }
    },

    maxLength() {
      if (
        !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.maxLength)
      ) {
        return this.fieldProps.fieldViewModel.maxLength;
      } else {
        if (
          this.field.type === 3 ||
          this.field.type === "Email" ||
          this.field.type === "Url" ||
          this.field.type === "Password"
        ) {
          return 190;
        } else if (this.field.type === 6) {
          return 300;
        } else {
          return 5000;
        }
      }
    },

    rows() {
      return !!this.fieldProps.fieldViewModel &&
        isNotNull(this.fieldProps.fieldViewModel.rows)
        ? this.fieldProps.fieldViewModel.rows
        : 3;
