    inputHint() {
      if (
        (this.formState === FormState.NEW && !!this.fieldProps.creatable) ||
        (this.formState === FormState.EDIT && !!this.fieldProps.updatable)
      ) {
        let hint = "";
        if (!this.fieldProps.nullable) {
          hint += "不可为空，";
        }
        if (
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.minValue)
        ) {
          hint += "大于等于" + this.fieldProps.fieldViewModel.minValue + "，";
        }
        if (
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.maxValue)
        ) {
          hint += "小于等于" + this.fieldProps.fieldViewModel.maxValue + "，";
        }
        if (
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.minLength)
        ) {
          hint +=
            "字符长度大于等于" +
            this.fieldProps.fieldViewModel.minLength +
            "，";
        }
        if (
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.maxLength)
        ) {
          hint +=
            "字符长度小于等于" + this.fieldProps.fieldViewModel.maxLength + "";
        }
        return hint;
      } else {
        return "";
      }
    },
