    formatPercent(event) {
      //formatPercent(value) {
      let newValue = parseInt(event.target.value);
      /* 输入无效数字后，event.target.value的值为‘’空字符串！！ */
      //if (isNaN(newValue) && (event.target.value !== '')) {
      if (!Number.isFinite(newValue) && newValue !== "") {
        event.target.focus();
        this.$message.error("数据格式不正确");
        return;
      } else if (isEmptyStr(event.target.value)) {
        event.target.value = null;
        this.fieldValue = null;
        return;
      }

      /* 改用字段校验函数实现
        newValue = this.checkMinValue(newValue)
        newValue = this.checkMaxValue(newValue)
        */
      this.fieldValue = newValue;
    },

    formatInteger(event) {
      let newValue = parseInt(event.target.value);
      /* 输入无效数字后，event.target.value的值为‘’空字符串！！ */
      //if (isNaN(newValue) && (event.target.value !== '')) {
      if (!Number.isFinite(newValue) && newValue !== "") {
        event.target.focus();
        this.$message.error("数据格式不正确");
        return;
      } else if (isEmptyStr(event.target.value)) {
        event.target.value = null;
        this.fieldValue = null;
        return;
      }

      /* 改用字段校验函数实现
        newValue = this.checkMinValue(newValue)
        newValue = this.checkMaxValue(newValue)
        */
      this.fieldValue = newValue;
    },

    formatDecimal(event) {
      let newValue = parseFloat(event.target.value);
      /* 输入无效数字后，event.target.value的值为‘’空字符串！！ */
      //if (isNaN(newValue) && (event.target.value !== '')) {
      if (!Number.isFinite(newValue) && newValue !== "") {
        event.target.focus();
        this.$message.error("数据格式不正确");
        return;
      } else if (isEmptyStr(event.target.value)) {
        event.target.value = null;
        this.fieldValue = null;
        return;
      }

      /* 改用字段校验函数实现
        newValue = this.checkMinValue(newValue)
        newValue = this.checkMaxValue(newValue)
        */
      newValue = parseFloat(
        Math.round(newValue * Math.pow(10, this.precision)) /
          Math.pow(10, this.precision)
      );

      this.fieldValue = newValue;
    },

    buildFieldRule() {
      if (
        this.formState === FormState.PREVIEW ||
        this.formState === FormState.VIEW ||
        (this.formState === FormState.NEW && !this.fieldProps.creatable) ||
        (this.formState === FormState.EDIT && !this.fieldProps.updatable)
      ) {
        return;
      }

      const requiredValidator = (rule, value, callback) => {
        /* 当字段为Boolean类型时，字段值false，isEmptyStr函数返回true，故须增加!==false判断 */
        if (
          (isEmptyStr(this.formModel[rule.field]) &&
            this.formModel[rule.field] !== false) ||
          this.formModel[rule.field] === null
        ) {
          callback(new Error("[" + rule.label + "]不能为空"));
        } else {
          callback();
        }
      };

      if (
        isNotNull(this.fieldProps.nullable) &&
        this.fieldProps.nullable === false
      ) {
        let requiredRule = {
          required: true,
          validator: requiredValidator,
          trigger: ["blur"],
          label: this.fieldProps.label /* 传入字段label用于错误提示！ */,
        };
        if (this.fieldProps.type === 6) {
          /* Tag字段需要设置校验对象type属性为array!! */
          requiredRule.type = "array";
          //newRule.trigger = ['change']
        }
        this.$emit("fieldValidate", requiredRule);
      }

      const minValidator = (rule, value, callback) => {
        if (
          !!this.fieldProps &&
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.minValue)
        ) {
          if (value < this.fieldProps.fieldViewModel.minValue) {
            callback(
              new Error(
                "输入值不能小于" + this.fieldProps.fieldViewModel.minValue
              )
            );
          } else {
            callback();
          }
        } else {
          callback();
        }
      };
      const maxValidator = (rule, value, callback) => {
        if (
          !!this.fieldProps &&
          !!this.fieldProps.fieldViewModel &&
          isNotNull(this.fieldProps.fieldViewModel.maxValue)
        ) {
          if (value > this.fieldProps.fieldViewModel.maxValue) {
            callback(
              new Error(
                "输入值不能大于" + this.fieldProps.fieldViewModel.maxValue
              )
            );
          } else {
            callback();
          }
        } else {
          callback();
        }
      };

      if (
        this.field.type === 1 ||
        this.field.type === "Percent" ||
        this.field.type === 2 ||
        this.field.type === "Money"
      ) {
        let minRule = {
          required: true,
          validator: minValidator,
          trigger: ["blur"],
          label: this.fieldProps.label /* 传入字段label用于错误提示！ */,
        };
        let maxRule = {
          required: true,
          validator: maxValidator,
          trigger: ["blur"],
          label: this.fieldProps.label /* 传入字段label用于错误提示！ */,
        };
        this.$emit("fieldValidate", minRule);
        this.$emit("fieldValidate", maxRule);
      }
    },

    showReferenceDialog(fieldName) {
      if (
        this.formState === FormState.PREVIEW ||
        this.formState === FormState.VIEW
      ) {
        return;
      }

      this.showReferenceDialogFlag = true;
      this.curRefField = fieldName;
    },

    addReferRecord(recordObj) {
      this.fieldValue = recordObj.id;
      this.fieldLabel = recordObj.label;
      this.showReferenceDialogFlag = false;
    },

    clearReference() {
      this.fieldValue = null;
      this.fieldLabel = null;
    },

    loadRoleList() {
      console.log(111111);
      // listRole()
      //   .then((res) => {
      //     if (res.error != null) {
      //       this.$message({ message: res.error, type: "error" });
      //       return;
      //     }

      //     this.roleList = res.data;
      //   })
      //   .catch((res) => {
      //     this.$message({ message: res.message, type: "error" });
      //   });
    },

    initFileList() {
      this.fileList.length = 0;
      let urlPrefix =
        this.field.type === 13
          ? baseURL + "/picture/get"
          : baseURL + "/file/get";
      if (!isEmptyStr(this.formModel[this.field.name])) {
        let fileNames = this.formModel[this.field.name];
        //let fileArray = []
        fileNames.split("||").forEach((item) => {
          if (!isEmptyStr(item)) {
            //fileArray.push(item)
            this.fileList.push({ name: item, url: urlPrefix + "/" + item });
          }
        });

        //this.fileList = [...new Set(fileArray)]  //去重
        //console.log(this.fileList)
        this.uploadBtnHidden = this.fileList.length >= this.maxFileCount;
      }
    },

    beforePictureUpload(file) {
      let fileTypeCheckResult = false;
      if (
        !!this.fieldProps &&
        !!this.fieldProps.fieldViewModel &&
        !!this.fieldProps.fieldViewModel.uploadFileTypes
      ) {
        let uploadFileTypes = this.fieldProps.fieldViewModel.uploadFileTypes;
        if (uploadFileTypes.length > 0) {
          fileTypeCheckResult = uploadFileTypes.some((ft) => {
            // console.log(idx);
            return file.type === "image/" + ft;
          });
        }
      }
      if (!fileTypeCheckResult) {
        this.$message.error(
          "上传图片不支持此格式: " + file.type + ", 请重新选择图片"
        );
        return false;
      }

      let fileSizeCheckResult = false;
      let uploadFileMaxSize = 5; //5MB
      if (
        !!this.fieldProps &&
        !!this.fieldProps.fieldViewModel &&
        !!this.fieldProps.fieldViewModel.fileMaxSize
      ) {
        uploadFileMaxSize = this.fieldProps.fieldViewModel.fileMaxSize;
      }
      fileSizeCheckResult = file.size / 1024 / 1024 <= uploadFileMaxSize;
      if (!fileSizeCheckResult) {
        this.$message.error(
          "上传图片大小不能超过" + uploadFileMaxSize + "MB, 请重新选择图片"
        );
        return false;
      }

      return true;
    },

    handlePictureUpload(res, file, fileList) {
      if (res.code === 200) {
        let fileName = res.data;
        file.name = fileName;
        file.url = baseURL + "/picture/get" + "/" + fileName;
        this.fileList.push(
          file
        ); /* 只能push file对象，如果push新的对象（如下一行）上传组件会出现抖动闪烁的问题！！ */
        //this.fileList.push({name: fileName, url: SYS.getPictureUrlPrefix() + '/' + fileName})

        let fileNamesStr = "";
        this.fileList.forEach((item) => {
          if (!isEmptyStr(item.name)) {
            fileNamesStr += item.name + "||";
          }
        });
        // this.formModel[this.field.name] = fileNamesStr
        this.uploadBtnHidden = fileList.length >= this.maxFileCount;
      }
    },

    handlePictureRemove(file, fileList) {
      //let newFileList = []
      let fileNamesStr = "";
      fileList.forEach((item) => {
        if (!isEmptyStr(item.name)) {
          //newFileList.push({name: item.name, url: item.url})
          fileNamesStr += item.name + "||";
        }
      });
      //this.fileList = newFileList
      this.fileList =
        fileList; /* 只能用fileList赋值，如果构建新对象赋值（如上一行）上传组件会出现抖动闪烁的问题！！ */
      // this.formModel[this.field.name] = fileNamesStr
      this.uploadBtnHidden = fileList.length >= this.maxFileCount;
    },

    handlePictureExceed() {
      let maxUploadCount = this.maxFileCount;
      this.$message.info(
        `最多允许上传${maxUploadCount}张图片, 请删除现有图片后上传`
      );
    },

    beforeFileUpload(file) {
      let fileTypeCheckResult = false;
      let extFileName = file.name.substring(file.name.lastIndexOf(".") + 1);
      if (
        !!this.fieldProps &&
        !!this.fieldProps.fieldViewModel &&
        !!this.fieldProps.fieldViewModel.uploadFileTypes
      ) {
        let uploadFileTypes = this.fieldProps.fieldViewModel.uploadFileTypes;
        if (uploadFileTypes.length > 0) {
          fileTypeCheckResult = uploadFileTypes.some((ft) => {
            return extFileName.toLowerCase() === ft.toLowerCase();
          });
        }
      }
      if (!fileTypeCheckResult) {
        this.$message.error(
          "上传文件不支持此格式: " + extFileName + ", 请重新选择文件"
        );
        return false;
      }

      let fileSizeCheckResult = false;
      let uploadFileMaxSize = 5; //5MB
      if (
        !!this.fieldProps &&
        !!this.fieldProps.fieldViewModel &&
        !!this.fieldProps.fieldViewModel.fileMaxSize
      ) {
        uploadFileMaxSize = this.fieldProps.fieldViewModel.fileMaxSize;
      }
      fileSizeCheckResult = file.size / 1024 / 1024 <= uploadFileMaxSize;
      if (!fileSizeCheckResult) {
        this.$message.error(
          "上传文件大小不能超过" + uploadFileMaxSize + "MB, 请重新选择图片"
        );
        return false;
      }

      return true;
    },

    handleFileUpload(res, file, fileList) {
      if (res.code === 200) {
        let fileName = res.data;
        file.name = fileName;
        file.url = baseURL + "/file/get" + "/" + fileName;
        this.fileList.push(
          file
        ); /* 只能push file对象，如果push新的对象（如下一行）上传组件会出现抖动闪烁的问题！！ */
        //this.fileList.push({name: fileName, url: SYS.getFileUrlPrefix() + '/' + fileName})

        let fileNamesStr = "";
        this.fileList.forEach((item) => {
          if (!isEmptyStr(item.name)) {
            fileNamesStr += item.name + "||";
          }
        });
        // this.formModel[this.field.name] = fileNamesStr
        this.uploadBtnHidden = fileList.length >= this.maxFileCount;
      }
    },

    handleFileRemove(file, fileList) {
      let fileNamesStr = "";
      fileList.forEach((item) => {
        if (!isEmptyStr(item.name)) {
          //newFileList.push({name: item.name, url: item.url})
          fileNamesStr += item.name + "||";
        }
      });
      //this.fileList = newFileList
      this.fileList =
        fileList; /* 只能用fileList赋值，如果构建新对象赋值（如上一行）上传组件会出现抖动闪烁的问题！！ */
      // this.formModel[this.field.name] = fileNamesStr
      this.uploadBtnHidden = fileList.length >= this.maxFileCount;
    },

    handleFileExceed() {
      let maxUploadCount = this.maxFileCount;
      this.$message.info(
        `最多允许上传${maxUploadCount}个文件, 请删除现有文件后上传`
      );
    },

    removeUploadFile(fileName) {
      let foundIdx = -1;
      this.fileList.forEach((file, idx) => {
        if (file.name === fileName) {
          foundIdx = idx;
        }
      });

      if (foundIdx >= 0) {
        this.fileList.splice(foundIdx, 1);

        let fileNamesStr = "";
        this.fileList.forEach((item) => {
          if (!isEmptyStr(item.name)) {
            fileNamesStr += item.name + "||";
          }
        });
        // this.formModel[this.field.name] = fileNamesStr
        this.uploadBtnHidden = this.fileList.length >= this.maxFileCount;
      }
    },

    handleAdd(filed, columns, fieldValue) {
      if (!Array.isArray(this.fieldValue)) {
        this.fieldValue = fieldValue = [];
      }
      setTimeout(() => {
        console.log(this.fieldValue);
        const json = {};
        columns.forEach((obj) => {
          const name = obj.prop;
          json[name] = "";
        });
        console.log(this.fieldValue);
        if (Array.isArray(this.fieldValue)) {
          this.fieldValue.push(json);
        } else {
          let arr = [];
          arr.push(json);
          this.fieldValue = arr;
        }
      });
    },
    handleUp(index) {
      let m = this.fieldValue.splice(index, 1)[0];
      this.fieldValue.splice(index - 1, 0, m);
    },
    handleDel(index) {
      this.fieldValue.splice(index, 1);
    },
    handleDown(index) {
      let m = this.fieldValue.splice(index, 1)[0];
      this.fieldValue.splice(index + 1, 0, m);
    },
  },
};
</script>

