<template>
  <el-form
    ref="dataForm"
    size="mini"
    :label-position="layout.labelPosition"
    :model="formModel"
    :rules="rules"
    :validate-on-rule-change="false"
    :hide-required-asterisk="formState == 3 ? true : false"
  >
    <el-tabs
      ref="formTabs"
      type="card"
      :value="layout.activeTabName"
      :class="{ 'hide-tab-header': hideTabHeader }"
    >
      <el-tab-pane
        v-for="tabItem in layout.formTabs"
        :key="tabItem.id"
        :name="tabItem.name"
        :label="tabItem.title"
      >
        <layout-section
          v-for="sectionItem in tabItem.sections"
          :key="sectionItem.id"
          accordion
          :value="['001']"
        >
          <layout-section-item
            :title="sectionItem.title"
            :disabled="false"
            name="001"
            :class="{
              'no-show-title': sectionItem.showSectionTitle === false,
              'no-show-splitter': sectionItem.showSplitter === false,
              'no-show-arrow': sectionItem.showArrowIcon === false,
            }"
          >
            <el-row
              v-for="rowItem in sectionItem.rows"
              :key="rowItem.id"
              :gutter="rowItem.gutter"
              :style="{
                'margin-bottom': !!layout.lineSpacing
                  ? layout.lineSpacing + 'px'
                  : '16px',
              }"
              style="margin-top: 2px; margin-right: 2px; margin-left: 2px"
            >
              <el-col
                v-for="cellItem in rowItem.cells"
                :key="cellItem.id"
                class="grid-cell"
                :span="cellItem.span"
              >
                <template
                  v-if="!!cellItem.fields && cellItem.fields.length > 0"
                >
                  <!-- 实体已删除字段 -->
                  <template
                    v-if="
                      cellItem.fields[0].deleted === true &&
                      formState != 1 &&
                      formState != 2
                    "
                  >
                    <div class="blank-cell">
                      {{ cellItem.fields[0].name }}已删除
                    </div>
                  </template>
                  <!-- 表单插槽 -->
                  <template v-else-if="cellItem.fields[0].type === 'Slot'">
                    <slot
                      :name="cellItem.fields[0].name"
                      :formModel="formModel"
                    >
                      <div class="blank-cell">
                        未设置插槽{{ cellItem.fields[0].name }}
                      </div>
                    </slot>
                  </template>
                  <!-- 表单自定义组件 -->
                  <template v-else-if="cellItem.fields[0].type === 'Custom'">
                    <component
                      :is="cellItem.fields[0].componentName"
                      :form-model="formModel"
                      :form-state="formState"
                      :data-fields="cellItem.fields[0].fields"
                      :field-props-map="fieldPropsMap"
                      :label-align="labelAlign"
                      :component-label="cellItem.fields[0].label"
                      :label-width="
                        !!layout.labelWidth ? layout.labelWidth : 75
                      "
                    ></component>
                  </template>
                  <!-- 此处可增加更多非数据绑定的表单组件，比如静态HTML内容、文字描述等等 -->

                  <el-form-item
                    v-else-if="formState == 3"
                    :label-width="
                      (!!cellItem.fields[0].labelWidth
                        ? cellItem.fields[0].labelWidth
                        : !!layout.labelWidth
                        ? layout.labelWidth
                        : 75) + ''
                    "
                    :label="
                      cellItem.fields[0].type == 'many'
                        ? ''
                        : fieldPropsMap[cellItem.fields[0].name].label
                    "
                    :class="[labelAlign]"
                    class="printItem"
                  >
                    <div
                      v-if="
                        cellItem.fields[0].type == 15 ||
                        cellItem.fields[0].type == 14
                      "
                      style="
                        padding: 0 5px;
                        height: 20px;
                        border: 1px solid #aaa;
                        display: inline-block;
                        text-align: center;
                        line-height: 20px;
                      "
                    >
                      {{ formModel[cellItem.fields[0].name] }}
                    </div>
                    <span
                      v-if="cellItem.fields[0].type != 'many'"
                      :class="
                        cellItem.fields[0].type == 15 ||
                        cellItem.fields[0].type == 14
                          ? 'chck'
                          : ''
                      "
                    >
                      <template
                        v-if="
                          cellItem.fields[0].type == 15 ||
                          cellItem.fields[0].type == 14
                        "
                      >
                        <span
                          v-for="(o, i) in fieldPropsMap[
                            cellItem.fields[0].name
                          ].optionList"
                          :key="i"
                          style="padding: 0 5px"
                        >
                          {{ o.val + " - " + o.name }}
                        </span>
                      </template>
                      <template v-else-if="cellItem.fields[0].type == 16">
                        <img
                          :src="formModel[cellItem.fields[0].name]"
                          style="width: 100%"
                        />
                      </template>
                      <template v-else-if="cellItem.fields[0].type != 'many'">
                        {{ formModel[cellItem.fields[0].name] }}
                      </template>
                    </span>
                    <table
                      v-if="cellItem.fields[0].type == 'many'"
                      style="width: 100%"
                      cellpadding="0"
                      cellspacing="0"
                      class="printTable"
                    >
                      <thead>
                        <tr>
                          <th
                            v-for="(th, i) in fieldPropsMap[
                              cellItem.fields[0].name
                            ].columnList"
                            :key="i"
                          >
                            {{ th.label }}
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(tr, trIndex) in formModel[
                            cellItem.fields[0].name
                          ]"
                          :key="trIndex"
                        >
                          <td
                            v-for="(th, i) in fieldPropsMap[
                              cellItem.fields[0].name
                            ].columnList"
                            :key="i"
                            :style="'text-align:' + th.align ? th.align : ''"
                          >
                            {{ tr[th.prop] }}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </el-form-item>
                  <field-widget
                    v-else
                    :ref="cellItem.fields[0].name"
                    :entity="entity"
                    :field="fieldPropsMap[cellItem.fields[0].name]"
                    :form-model="formModel"
                    :labels-model="labelsModel"
                    :form-state="formState"
                    :label-align="labelAlign"
                    :field-props="fieldPropsMap[cellItem.fields[0].name]"
                    :field-props-map="fieldPropsMap"
                    :layout="layout"
                    :label-width="
                      !!cellItem.fields[0].labelWidth
                        ? cellItem.fields[0].labelWidth
                        : !!layout.labelWidth
                        ? layout.labelWidth
                        : 75
                    "
                    :userrole="userrole"
                    :one-to-one-childs="oneToOneChilds"
                    @setFormModel="setValue"
                    @setAttr="setAttr"
                    @setChildrens="setChildrens"
                    @fieldValidate="
                      (validatorRule) =>
                        addFieldValidator(
                          cellItem.fields[0].name,
                          validatorRule
                        )
                    "
                    @openSubForm="openSubForm"
                  ></field-widget>
                </template>
                <template v-else-if="isPreview">
                  <div class="blank-cell">--空白栅格--</div>
                </template>
                <template v-else>
                  <div class="blank-cell"></div>
                </template>
              </el-col>
            </el-row>
          </layout-section-item>
        </layout-section>
      </el-tab-pane>
    </el-tabs>

    <el-dialog
      :title="SubformTitle"
      :visible.sync="showReferenceDialogFlag"
      :show-close="true"
      width="70%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
    >
      <FormWidget
        ref="SubForm"
        :entity="entity"
        :layout="formWidgetLayout"
        :form-model="formWidgetFormModel"
        :field-props-map="formWidgetFieldPropsMap"
        :labels-model="formWidgetLabelsModel"
        :form-state="formState"
        :userrole="userrole"
        @setFormModel="setValue2"
        @setChildrens="setChildrens"
      />
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="showReferenceDialogFlag = false">
          取 消
        </el-button>
      </div>
    </el-dialog>
  </el-form>
</template>

<script>
import LayoutSection from "@/components/LayoutSection/";
import LayoutSectionItem from "@/components/LayoutSection/src/collapse-item";
import FieldWidget from "./field-widget";
import FormState from "../form-state-variables";
// import { isNotNull, isEmptyStr } from "@/utils/util";
import FormValidators from "@/utils/validators";
import { formchildQuery } from "@/api";
import Vue from 'vue'
import { createLayoutObj } from '../layout/form-layout-object'

export default {
  name: "FormWidget",
  components: { LayoutSection, LayoutSectionItem, FieldWidget },
  props: {
    oneToOneChilds: {
      type: Array,
      default: () => {
        return [];
      },
    },
    userrole: {
      type: Object,
      default: () => {},
    },
    entity: {
      type: Object,
      default: () => {
        return {};
      },
    },
    layout: {
      type: Object,
      default: () => {
        return {};
      },
    },
    formModel: {
      type: Object,
      default: () => {
        return {};
      },
    },
    labelsModel: {
      type: Object,
      default: () => {},
    },
    fieldPropsMap: {
      type: Object,
      default: () => {},
    },
    formState: {
      type: Number,
      default: null,
    },

    //传入的表单验证规则
    extraRules: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      rules: {},
      fieldRules: {},
      showReferenceDialogFlag: false,
      formWidgetLayout: {},
      formWidgetFormModel: {},
      formModelCopy: {},
      formWidgetLabelsModel: {},
      formWidgetFieldPropsMap: {},
      formWidgetFormState: {},
      SubformTitle: "",
      tableName: "",
      tableId: "",
    };
  },
  computed: {
    labelAlign() {
      return !!this.layout && !!this.layout.labelAlign
        ? this.layout.labelAlign
        : "label-left-align";
    },

    isPreview() {
      return this.formState === FormState.PREVIEW;
    },

    hideTabHeader() {
      return (
        !!this.layout &&
        !!this.layout.formTabs &&
        this.layout.formTabs.length === 1 &&
        this.layout.hideOnlyTabTitle === true
      );
    },
  },

  mounted() {
    this.buildFormRules();
  },

  methods: {
    setChildrens(data) {
      this.$emit("setChildrens", data);
    },
    setValue2(data) {
      this.formWidgetFormModel[data.key] = data["value"];
    },
    openSubForm(data) {
      this.tableId = data.tableId;
      this.tableName = data.tableName;
      this.SubformTitle = data.subformTitle;
      this.addNewRecord();
    },
    buildLayoutObj() {
      let eventHub = new Vue()
      return createLayoutObj(eventHub)
    },
    handleDeletedFields(res) {
      let deletedFields = res.t.deletedFields
      if (!!deletedFields && Array.isArray(deletedFields)) {
        let layoutFieldWrappers = this.layout.getAllLayoutFieldWrappers()
        if (!!layoutFieldWrappers && Array.isArray(layoutFieldWrappers)) {
          layoutFieldWrappers.forEach((fw) => {
            let deletedFlag = arrayContain(deletedFields, fw.name)
            if (deletedFlag) {
              fw.deleted = true /* 设置已删除字段标志 */
            }
          })
        }
      }
    },
    async addNewRecord() {
      const res = await formchildQuery({
        data_id: this.dataid,
        table_id: this.tableId,
        table_name: this.tableName,
        isprint: this.formState == 3 ? 1 : null,
      });
      if (res.state == "error") {
        this.$message({ message: res.error, type: "error" });
        return;
      }
      if (!!res.t && !!res.t.layoutJson) {
        // this.curRecordId = null;
        this.formWidgetLayout = this.buildLayoutObj();
        this.formWidgetLayout.setLayoutPropsFromServer(res, "add");
        this.handleDeletedFields(res); /* 处理表单已删除字段！！ */
        if (
          this.childrenData &&
          this.childrenData[this.tableName] &&
          this.childrenData[this.tableName].all
        ) {
          this.formWidgetFormModel = this.childrenData[this.tableName].all;
          this.formModelCopy = JSON.parse(
            JSON.stringify(this.childrenData[this.tableName].all)
          );
        } else {
          this.formWidgetFormModel = res.t.formData;
          this.formModelCopy = JSON.parse(JSON.stringify(res.t.formData));
        }

        this.formWidgetLabelsModel = res.t.labelData;
        this.formWidgetFieldPropsMap = res.t.fieldPropsMap;
        // this.formWidgetFormState = FormState.NEW
        this.showReferenceDialogFlag = true;
        if (this.$refs["SubForm"]) {
          this.$refs["SubForm"].clearFormValidate();
        }
      } else {
        this.$message({ message: "加载表单布局出错", type: "error" });
      }
    },
    toSubForm(item) {
      this.$nextTick(() => {
        this.openSubForm({
          tableId: item.table_id,
          tableName: item.table_name,
          subformTitle: item.table_name_zh,
        });
      });
    },
    setAttr({ fields, attr, flag }) {
      if (fields && fields.length > 0) {
        fields.forEach((field) => {
          const ref = this.$refs[field];
          if (ref && ref.length > 0) {
            ref[0].field[attr] = flag;
            if (attr != "is_null") {
              ref[0].field["is_null"] = flag == 1 ? 0 : 1;
            }
            ref[0].$forceUpdate();
            this.setValue({
              key: field,
              value: "",
            });
          }
        });
      }
      console.log(this.fieldPropsMap);
      console.log(this.$refs);
    },
    setValue(value) {
      this.$emit("setFormModel", value);
    },
    buildFormRules() {
      const result = {};
      //const result = this.rules
      if (this.fieldPropsMap) {
        let propsMap = this.fieldPropsMap;
        //console.log( propsMap )
        for (let fieldName in propsMap) {
          if (!propsMap.hasOwnProperty.call(fieldName)) {
            continue;
          }

          if (
            this.formState === FormState.PREVIEW ||
            this.formState === FormState.VIEW ||
            (this.formState === FormState.NEW &&
              !propsMap[fieldName].creatable) ||
            (this.formState === FormState.EDIT &&
              !propsMap[fieldName].updatable)
          ) {
            continue;
          }

          /* 添加字段内置的校验函数 */
          if (
            !!this.fieldRules &&
            this.fieldRules.hasOwnProperty.call(fieldName)
          ) {
            if (!result[fieldName]) {
              result[fieldName] = new Array(0);
            }

            if (!Array.isArray(this.fieldRules[fieldName])) {
              result[fieldName].push(this.fieldRules[fieldName]);
            } else {
              this.fieldRules[fieldName].forEach((fRule) => {
                result[fieldName].push(fRule);
              });
            }
          }

          /* 添加字段自定义校验函数 */
          if (
            !!propsMap[fieldName].fieldViewModel &&
            !!propsMap[fieldName].fieldViewModel.validators
          ) {
            //console.log(propsMap[fieldName].fieldViewModel.validators)
            if (!result[fieldName]) {
              result[fieldName] = new Array(0);
            }

            propsMap[fieldName].fieldViewModel.validators.forEach((vdt) => {
              if (FormValidators.hasOwnProperty.call(vdt)) {
                result[fieldName].push({
                  validator: FormValidators[vdt],
                  trigger: ["blur"],
                  label: propsMap[fieldName].label,
                });
              }
            });
          }

          /* 添加表单附加的校验函数 */
          if (
            !!this.extraRules &&
            this.extraRules.hasOwnProperty.call(fieldName)
          ) {
            if (!result[fieldName]) {
              result[fieldName] = new Array(0);
            }

            if (!Array.isArray(this.extraRules[fieldName])) {
              result[fieldName].push(this.extraRules[fieldName]);
            } else {
              this.extraRules[fieldName].forEach((fRule) => {
                result[fieldName].push(fRule);
              });
            }
          }
        }
      }

      this.rules = result;
    },

    validateForm(callback) {
      this.$refs["dataForm"].validate(callback);
    },

    clearFormValidate() {
      this.$refs["dataForm"].clearValidate();
    },

    /* 接收单个字段组件传入的校验规则 */
    addFieldValidator(fieldName, validatorRule) {
      //console.log('KKKKKKKKKKKK')
      if (!this.fieldRules[fieldName]) {
        this.fieldRules[fieldName] = new Array(0);
      }
      this.fieldRules[fieldName].push(validatorRule);
    },
  },
};
</script>

