  font-size: 11px;
  //font-style: italic;
  height: auto;
  line-height: normal;
  padding-bottom: -2px;
  border-bottom: 1px solid #eeeeee;
  margin-bottom: 6px;
  width: 100%;
}

.el-collapse-item.no-show-title ::v-deep .el-collapse-item__header {
  display: none !important;
}

.el-collapse-item.no-show-splitter ::v-deep .el-collapse-item__header {
